<template>
	<div class="router-page">
		<header-bar :showGold="true"></header-bar>
		<div class="error">
			<div class="wrap-404">
				<span class="four">4</span><img src="../assets/graphics/feelsbadman.png" class="feelsbadman"><span class="four">4</span>
			</div>
			<h1 class="yellow">Sorry Bro</h1>
			<h2>Something went wrong</h2>
			<h2>We couldn't find what you were looking for</h2>
			<a href="./" class="back">BACK HOME</a>
		</div>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import HeaderBar from './globals/Header'
import FooterBar from './globals/Footer'

export default {
	name: 'app',
	components: {
		HeaderBar,
		FooterBar
	}
}
</script>
<style scoped lang="scss">
@import "../variables.scss";
h1{
	margin: -10px 0 15px 0;
}
h2{
	color: $font-white;
	text-transform: none;
}
.back{    
	display: block;
	width: 80%;
	max-width: 175px;
	padding: 10px;
	margin: 50px auto;
	background-color: $yellow-dark;
	border-radius: 5px;
	color: $gray-dark;
	font-weight: bolder;
	transition: all .15s ease-in-out;;
}
.back:hover, .back:focus{
	background-color: $yellow-light;
	transform: scale(1.05);
}
.back:active{
	background-color: $yellow-med;
}
.error{
	margin-top: 10vh;
	transition: $standard-transition;
}
.feelsbadman{
	width: 300px;
	transition: $standard-transition;
}
.four{
	font-size: 400px;
	line-height: .75%;
	color: $gray-light;
	transition: $standard-transition;
}
.wrap-404{
	position: relative;
	margin: 50px auto;
}
@media screen and (max-width: 55.000em){
	.feelsbadman{
		width: 200px;
	}
	.four{
		font-size: 265px;
	}
}
@media screen and (max-width: 45.000em){
	.feelsbadman{
		width: 150px;
	}
	.four{
		font-size: 195px;
	}
}
</style>


