</template>

<script>
export default {
	name: 'member-tile',
	computed: {
		twitchData: function () {
			return this.$store.state.TwitchInfo[this.member.id]
		},
		schedule: function () {
			return this.member.schedule
		},
		schedule2: function () {
			return this.member.schedule2 ? this.member.schedule2 : 'none'
		},
		logo: function () {
			return this.twitchData ? this.twitchData.logo : '../../../static/profiles/' + this.member.display_name + '.png'
		},
		url: function () {
			return this.twitchData ? this.twitchData.url : 'https://www.twitch.tv/' + this.member.display_name
		},
		live: function () {
			return this.twitchData ? this.twitchData.live : null
		},
		viewers: function () {
			return this.live ? this.live.viewers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : 0
		},
		followers: function () {
			return this.twitchData ? this.twitchData.followers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') + ' Followers' : null
		}
	},
	props: ['member']
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";
h2{
	display: inline-block;
	text-transform: capitalize;
}
.member-tile {
	width: 465px;
	display: inline-block;
	margin: 20px;
	vertical-align: top;
	text-align: left;
	img {
		display: inline-block;
		width: 90px;
		margin: 10px 13px;
		border-radius: 100%;
		padding: 4px;
		vertical-align: middle;
	}
	.top-bar-subwrap{
		display: inline-block;
		vertical-align: middle;
	}
	.top-bar{
		background-color: $gray-dark;
		box-shadow: $bs;
	}
	.middle-bar{
		font-size: .95em;
    line-height: 15px;
    letter-spacing: 0.09em;
		background-color: $yellow-dark;
		color: $gray-dark;
    display: flex;
    align-items: center;
		justify-content: center;
    height: 45px;
    margin: 10px 0;
		box-shadow: $bs;
		&.non-streamer{
			justify-content: space-around;
		}
	}
	.bottom-bar{
		display: flex;
		align-items: center;
		background-color: $gray-dark;
		padding: 0 25px;
		height: 185px;
		text-align: left;
		box-shadow: $bs;
	}
	.twitch-url{
		display: inline-block;
		background-color: $yellow-dark;
		color: $gray-dark;
		margin: 5px 0;
		padding: 5px 10px;
		border-radius: 3px;
		transition: $standard-transition;
		&:hover{
			background-color: $yellow-light;
			box-shadow: 0 0 1px 1px $yellow-dark;
		}
	}
	.icon-wrapper{
		margin: 5px 0;
	}
	.followers{
		display: inline-block;
		margin-right: 10px;
	}
	.schedule{
		display: inline-block;
	}
	.live{
		border-radius: 3px;
	}
	.role{
		display: inline-flex;
		align-items: center;
		color: $gray-dark;
		text-transform: capitalize;
	}
	.manager-email{
		display: inline-flex;
		align-items: center;
		a{
			color: $gray-dark;
		}
	}
	&.community-manager{
		h2{
			color: $gray-dark;
		}
		.top-bar{
			background-color: $yellow-dark;
		}
		.twitch-url{
			background-color: $gray-dark;
			color: $font-white;
		}
		.icon{
			-webkit-filter: brightness(20%) contrast(85%) grayscale(100%);
			-moz-filter: brightness(20%) contrast(85%) grayscale(100%);
			-ms-filter: brightness(20%) contrast(85%) grayscale(100%);
			-o-filter: brightness(20%) contrast(85%) grayscale(100%);
			filter: brightness(20%) contrast(85%) grayscale(100%);
			&:hover, &:active, &:focus{
				-webkit-filter: brightness(0%);
				-moz-filter: brightness(0%);
				-ms-filter: brightness(0%);
				-o-filter: brightness(0%);
				filter: brightness(0%);
			}
		}
		.role{
			background-color: $live-red;
			padding: 3px 5px;
			border-radius: 3px;
			color: $font-white;
			.icon{
				fill: $font-white;
				-webkit-filter: brightness(100%);
				-moz-filter: brightness(100%);
				-ms-filter: brightness(100%);
				-o-filter: brightness(100%);
				filter: brightness(100%);
			}
		}
	}
}
#lowco2525-tile img{
	border: 2px solid $lowco-purple;
}
#smashley-tile img{
	border: 2px solid $smashley-blue;
}
#djtechlive-tile img{
	border: 2px solid $dj-blue;
}
#bloodyfaster-tile img{
	border: 2px solid $bloody-red;
}
#odysseygrrrl-tile img{
	border: 2px solid $gray-dark;
}
.icon {
	display: inline-block;
	transition: all .2s ease-in-out;
	width: 20px;
	height: 20px;
	margin-right: 10px;
	-webkit-filter: grayscale(100%) brightness(75%);
	-moz-filter: grayscale(100%) brightness(75%);
	-ms-filter: grayscale(100%) brightness(75%);
	-o-filter: grayscale(100%) brightness(75%);
		filter: grayscale(100%) brightness(75%);
	&:hover{
		-webkit-filter: grayscale(0%) brightness(100%);
		-moz-filter: grayscale(0%) brightness(100%);
		-ms-filter: grayscale(0%) brightness(100%);
		-o-filter: grayscale(0%) brightness(100%);
			filter: grayscale(0%) brightness(100%);
	}
	&.twitter {
		background: url("../../assets/svg/twitter.svg") no-repeat 50% 50%;
	}
	&.discord {
		background: url("../../assets/svg/discord.svg") no-repeat 50% 50%;
	}
	&.email {
		background: url("../../assets/svg/email.svg") no-repeat 50% 50%;
		background-size: cover;
	}
	&.instagram {
		background: url("../../assets/svg/instagram.svg") no-repeat 50% 50%;
	}
	&.youtube {
		background: url("../../assets/svg/youtube.svg") no-repeat 50% 50%;
	}
}
</style>


