<template>
	<div class="about router-page">
		<header-bar :showGold="false"></header-bar>
		<div class="tile-wrapper">
			<about-tile v-for="member in members" :member="member"></about-tile>
		</div>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import Members from '../../static/memberInfo.json'
import HeaderBar from './globals/Header'
import AboutTile from './about/AboutTile'
import FooterBar from './globals/Footer'
import { mapActions } from 'vuex'

export default {
	name: 'about',
	components: {
		HeaderBar,
		AboutTile,
		FooterBar
	},
	data () {
		return {
			members: Members
		}
	},
	methods: {
		...mapActions([
			'updateChannelInfoList',
			'getChannelInfo'
		])
	},
	mounted () {
		this.updateChannelInfoList().catch(function () {
			console.log('There was a problem fetching Twitch data')
		})
	}
}
</script>

<style scoped lang="scss">
@import "../variables.scss";
.tile-wrapper{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 25px auto;
}
@media screen and (max-width: 77.000em){
	.tile-wrapper{
		width: 100%;
	}
}
</style>



